import React from "react"
import { graphql } from 'gatsby'

import Layout from "/src/layouts/main/layout/layout.js"
import Seo from "../components/seo"
import ThoughtsList from '../components/ThoughtsList/ThoughtsList'


const ThoughtsPage = ({ data }) => {
  
  return (
    <Layout>
      <Seo title="Thoughts" />
      {<ThoughtsList thoughts={ data.allMarkdownRemark.nodes } />}
    </Layout>
  )
}

export default ThoughtsPage

export const pageQuery = graphql`query ThoughtsQuery {
    allMarkdownRemark(
      sort: {fields: [frontmatter___date], order: DESC}
      limit: 1000
      filter: {fields: {collection: {eq: "thoughts"}}, frontmatter: {visibility: {eq: "public"}}}
    ) {
      nodes {
        id
        fields {
          slug
        }
        frontmatter {
          title
          date(formatString: "MMMM DD, YYYY", locale: "es")
        }
      }
    }
  }  
`