import React from 'react'
import { Link } from 'gatsby'

const ThoughtsItem = ({ thought }) => {
    const { title } = thought.frontmatter
    const { slug } = thought.fields
    return (
        <Link className="thoughts-item__link" to={`/thoughts${slug}`}>
            <div className="thoughts-item">
                <div className="thoughts-item__content">
                    <h3 className="thoughts-item__title">
                        {title}
                    </h3>
                </div>            
            </div>
        </Link>
    );

}

export default ThoughtsItem